@import "https://fonts.googleapis.com/css?family=Montserrat:400,700,900&display=swap";
:root {
  --gradient: linear-gradient(135deg, #72edf2 10%, #5151e5 100%);
}

* {
  box-sizing: border-box;
  line-height: 1.25em;
}

body {
  background-image: url("weather-2.aa4e6dd5.gif");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.body-container {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: Montserrat, sans-serif;
  display: flex;
}

.container {
  color: #fff;
  height: 600px;
  width: 390px;
  background-color: rgba(34, 40, 49, .4);
  border-radius: 25px;
  flex-direction: column;
  display: flex;
}

.weather-side {
  text-align: center;
  width: 100%;
  background-color: rgba(145, 214, 237, .6);
  border-radius: 25px;
}

.weather-temp {
  margin: 0;
  font-size: 3em;
  font-weight: 700;
}

.weather-desc {
  margin: 0;
}

.info-side {
  float: left;
  height: 100%;
  padding-top: 25px;
  position: relative;
}

.today-info {
  text-transform: uppercase;
  border-radius: 10px;
  justify-content: space-between;
  margin: 5px 25px;
  padding: 15px;
  display: flex;
  box-shadow: 0 0 50px -5px rgba(0, 0, 0, .25);
}

.location-button {
  width: 100%;
  box-sizing: border-box;
  background-image: var(--gradient);
  color: #fff;
  cursor: pointer;
  -o-transition: transform 200ms ease;
  border: none;
  border-radius: 25px;
  outline: none;
  padding: 10px;
  font-family: Montserrat, sans-serif;
  font-weight: 700;
  transition: transform .2s, -webkit-transform .2s;
  box-shadow: 0 0 30px -5px rgba(0, 0, 0, .25);
}

.temp-container {
  justify-content: center;
  gap: 70px;
  display: flex;
}

.form {
  max-width: 100%;
  width: 390px;
  text-shadow: .0625rem .0625rem .0625rem #fff;
  border-radius: .5rem;
  justify-content: space-between;
  align-items: center;
  column-gap: 10px;
  margin-bottom: 25px;
  padding: 8px 12px;
  font-size: 1em;
  display: flex;
  box-shadow: -3px -3px 20px #fff, 5px 5px 20px #919dcc;
}

.input {
  width: 90%;
  color: #000;
  background-color: rgba(255, 255, 255, .75);
  border-bottom: 2px solid rgba(255, 255, 255, .25);
  border-radius: 5px;
  padding: .7em;
}

.btn {
  text-shadow: .5px .5px #fff;
  cursor: pointer;
  background-color: rgba(255, 255, 255, .9);
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: .5rem;
  display: flex;
  box-shadow: -3px -3px 15px #fff, 3px 3px 15px #919dcc;
}

